___CSS_LOADER_EXPORT___.push([module.id, `body {
  margin: 40px 40px 0 40px;
  gap: 250px;
  padding: 0;

  display: flex;
  flex-direction: column;

  font-family: 'Inter';

  background-color: #363534;

  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
}

.header-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-text-container {
  padding: 40px;
  background-color: white;
  min-height: 400px;
  min-width: 400px;
}

#header-text {
  font-size: 1.6rem;
  font-weight: 300;
  opacity: 0;
  transition: opacity 1s ease-in;
}

#header-text.show {
  opacity: 1;
}

#header-pic {
  max-width: 400px;
}

#header-name {
  font-size: 3rem;
  font-style: italic;
  position: absolute;
  left: 0;
  top: 0;
  min-width: 400px;

  display: flex;
  justify-content: center;
  align-items: center;

  color: black;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin: 0 auto;
}

.project {
  background: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  width: 350px;
  height: 250px;
  transition: all 0.3s ease-in;
  position: relative;
  overflow: hidden;
}

.project-description-overlay {
  position: absolute;
  bottom: 0px;
  left: 0;
  right: 0;
  padding: 10px;
  opacity: 0;
  visibility: hidden;
  transition:
    opacity 0.4s ease,
    visibility 0.4s ease;
  display: flex;
  justify-content: center;
  text-align: center;
}

.project-description-overlay.visible {
  opacity: 1;
  visibility: visible;
}

.project:hover {
  height: 400px;
}

.project:hover .project-label {
  position: absolute;
  top: 20px;
}

.project-label {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 2rem;
  cursor: pointer;
  margin-bottom: auto;
  position: relative;
  bottom: 0;
}

.icon {
  width: 40px;
  cursor: pointer;
}

.icon{
  transition: transform 0.3s ease;
}

.icon:hover {
  transform: scale(1.2);
}

.hoverable-text {
  text-decoration: none;
  transition: text-decoration 0.3s ease;
}

.hoverable-text:hover {
  text-decoration: underline;
}

.project-image {
  width: 100%;
  margin-bottom: auto;
  border-radius: 12px 12px 0 0;
}

.footer-container {
  background-color: #000000;
  color: #f6f5f4;
  width: 100vw;
  text-align: center;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  margin-top: auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

#footer-img {
  max-width: 300px;
  margin-right: 40px;
}

.left-footer {
  margin-left: 40px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

#contact-me {
  font-weight: 400;
  font-size: 2rem;
}

#get-in-touch {
  max-width: 300px;
}

#phone-icon, #email-icon {
  background-color: #f6f5f4;
  color: #363534;
  width: 20px;
  border-radius: 20px;
  padding: 4px;
}

.phone-container, .email-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 8px;
}


@media (max-width: 1635px) {
  .projects-container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 1214px) {
  .projects-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 900px) {
  body {
    margin: 30px 30px 0 30px;
  }

  .header-container {
    flex-direction: column;
  }

  #header-pic {
    max-width: 350px;
  }

  .header-text-container {
    margin: 40px;
    background-color: white;
    min-height: 400px;
    max-height: 620px;
  }

  #header-name {
    font-size: 2.4rem;

    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    min-width: 350px;
    max-height: 50px;
  }

  .footer-container {
    gap: 40px;
  }
}

@media (max-width: 840px) {
  .projects-container {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 600px) {
  .header-container {
    flex-direction: column;
  }

  #header-pic {
    max-width: 300px;
  }

  .header-text-container {
    margin: 40px;
    background-color: white;
    min-height: 200px;
  }

  #header-name {
    font-size: 2rem;
  }

  body {
    margin: 20px 20px 0 20px;
  }

  .projects-container {
    grid-template-columns: 1fr;
  }

  .footer-container {
    gap: 40px;
  }

  #contact-me {
    font-size: 1.5rem;
  }
    
  .left-footer {
    font-size: 1rem;
  }

  #footer-img {
    width: 200px;
  }
}

@media (max-width: 500px){
  #header-text {
    font-size: 1.3rem;
  }
  #footer-img {
    width: 150px;
  }
}

@media (max-width: 400px){
  .project {
    transform: scale(0.8);
    position: relative;
  }
}
